.header-image {
	width: 100%;
	height: 455px;
	background-image: url(../img/contacts-bg.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}

.content-wrap.cont {
	align-items: flex-start;
	flex-wrap: wrap;

	@include breakpoint(max-width 1100px) {
		justify-content: center;
		text-align: center;
	}

	@include breakpoint(max-width 576px) {
		max-width: 90vw;
	}
	.contacts__menu,
	.links {
		width: 150px;

		@include breakpoint(max-width 1100px) {
			max-width: 560px;
			width: 90vw;
		}
		p{
			margin-top: 25px;
		}

		a {
			font-family: $gil-medium;
			font-size: 18px;
			color: $purple;
		}

		a {
			&:hover, &:active, &focus {
				color: $purple-hover;
				text-decoration: underline;
			}
		}
	}

	.contacts__contact {
		display: flex;
		flex-direction: column;
		@include breakpoint(max-width 1100px) {
			align-items: center;
		}

		.contacts__title {
			max-width: 560px;
			width: 90vw;
			color: #282828;
			h2 {
				margin-top: 20px;
				font-family: $gil-regular;
				font-weight: normal;
				font-size: 24px;
			}

			p {
				font-family: $gil-regular;
				font-size: 18px;
			}

			.mail {
				font-size: 11px;
				a {
					font-family: $gil-regular;
					font-size: 12px;
					color: #282828;
					&:hover, &:active, &focus {
						text-decoration: underline;
					}
				}
			}
		}
		.contacts__form {
			#form {
				max-width: 360px;
				width: 90vw;
				display: flex;
				flex-direction: column;
				align-items: center;
				legend {
					margin: 20px 0 20px;
					font-family: $gil-regular;
					font-size: 24px;
				}

				label {
					margin: 20px 0 5px;
					font-family: $gil-light;
					font-size: 18px;
				}

				input, textarea {
					max-width: 360px;
					width: 90vw;
					padding: 10px 5px;
					background-color: #ece9e4;
					border: 1px solid #e6e2db;
					border-radius: 5px;
					box-sizing: border-box;
				}

				textarea {
					height: 50px;
					margin-bottom: 20px;
				}

				input[type="submit"] {
					margin-top: 10px;
					margin-bottom: 50px;
					font-size: 20px;
					background-color: $purple;
					color: white;
					border: none;
					&:hover {
						background-color: $purple-hover;
						cursor: pointer;
					}
				}
			}
		}
	}
}
